
    <template>
      <section class="market-doc-container">
        <h2>Slider 滑块</h2>
<h3>基本使用</h3>
<p>slider支持拖拽和点击改变其值。</p>
<mku-example>
        
        <template slot="source"><element-demo0 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;mku-row :gutter=&quot;20&quot;&gt;
    &lt;mku-col span=&quot;24&quot; class=&quot;doc-mb20&quot;&gt;
      &lt;mku-slider :value=&quot;20&quot; /&gt;
    &lt;/mku-col&gt;
    &lt;mku-col span=&quot;24&quot;&gt;
      &lt;mku-slider v-model=&quot;value&quot; /&gt;
    &lt;/mku-col&gt;
  &lt;/mku-row&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      value: 50
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>离散值</h3>
<mku-example>
        
        <template slot="source"><element-demo1 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;mku-row :gutter=&quot;20&quot;&gt;
    &lt;mku-col span=&quot;24&quot; class=&quot;doc-mb20&quot;&gt;
      &lt;p class=&quot;doc-mb10&quot;&gt;隐藏断点&lt;/p&gt;
      &lt;mku-slider v-model=&quot;value&quot; :step=&quot;10&quot; /&gt;
    &lt;/mku-col&gt;
    &lt;mku-col span=&quot;24&quot;&gt;
      &lt;p class=&quot;doc-mb10&quot;&gt;显示断点&lt;/p&gt;
      &lt;mku-slider v-model=&quot;value2&quot;  :step=&quot;10&quot; show-stops /&gt;
    &lt;/mku-col&gt;
  &lt;/mku-row&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      value: 20,
      value2: 30,
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>起止值</h3>
<mku-example>
        
        <template slot="source"><element-demo2 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;mku-row :gutter=&quot;20&quot;&gt;
    &lt;mku-col span=&quot;24&quot; class=&quot;doc-mb20&quot;&gt;
      &lt;mku-slider
        v-model=&quot;value&quot;
        show-stops
        :min=&quot;30&quot;
        :max=&quot;50&quot;
      /&gt;
    &lt;/mku-col&gt;
    &lt;mku-col span=&quot;24&quot;&gt;
      &lt;mku-slider
        v-model=&quot;value2&quot;
        :min=&quot;80&quot;
        :max=&quot;120&quot;
      /&gt;
    &lt;/mku-col&gt;
  &lt;/mku-row&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      value: 35,
      value2: 92,
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>选择范围</h3>
<mku-example>
        
        <template slot="source"><element-demo3 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;mku-row :gutter=&quot;20&quot;&gt;
    &lt;mku-col span=&quot;24&quot; class=&quot;doc-mb20&quot;&gt;
      &lt;p class=&quot;doc-mb10&quot;&gt;value:{{value}}&lt;/p&gt;
      &lt;mku-slider
        v-model=&quot;value&quot;
        range
        show-stops
        :min=&quot;50&quot;
        :max=&quot;80&quot;
        :step=&quot;4&quot;
      /&gt;
    &lt;/mku-col&gt;
    &lt;mku-col span=&quot;24&quot;&gt;
      &lt;p class=&quot;doc-mb10&quot;&gt;value:{{value2}}&lt;/p&gt;
      &lt;mku-slider
        v-model=&quot;value2&quot;
        range
        show-stops
        :min=&quot;10&quot;
        :max=&quot;150&quot;
        :step=&quot;10&quot;
      /&gt;
    &lt;/mku-col&gt;
  &lt;/mku-row&gt;
&lt;/template&gt;

&lt;script&gt;
