</code></pre></template></mku-example><h3>自定义标记</h3>
<mku-example>
        
        <template slot="source"><element-demo5 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;mku-slider v-model=&quot;value&quot; :masks=&quot;masks&quot; /&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      value: 38,
      masks: {
        0: '0ml',
        10: '10ml',
        30: '30ml',
        50: h =&gt; {
          return [
            h('span', {
              style: {
                color: '#2d8cf0'
              }
            }, '标准值 '),
            h('strong', {
              style: {
                color: '#2d8cf0'
              }
            }, 50)
          ]
        },
        80: h =&gt; h('span', {
          style: {
            color: '#f00'
          }
        }, '极限值 80')
      }
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>Drawer Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:center">类型</th>
<th style="text-align:center">可选值</th>
<th style="text-align:center">默认值</th>
<th style="text-align:center">必填</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">绑定的值，可使用<code>v-model</code>双向绑定</td>
<td style="text-align:center"><code>Number | Array</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>0</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">min</td>
<td style="text-align:left">选择区间的最小值</td>
<td style="text-align:center"><code>Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>0</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">max</td>
<td style="text-align:left">选择区间的最大值</td>
<td style="text-align:center"><code>Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>100</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">step</td>
<td style="text-align:left">步长</td>
<td style="text-align:center"><code>Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>1</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">showStops</td>
<td style="text-align:left">是否展示离散值的断点</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>false</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">range</td>
<td style="text-align:left">开启范围选择，开启后<code>value</code>绑定的值为Array</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>false</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">masks</td>
<td style="text-align:left">自定义标记，<code>key</code>值必须为<code>Number</code>，值为<code>String</code>直接展示，值为<code>Function</code>时作为render函数时候</td>
<td style="text-align:center"><code>Object</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">禁用</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>false</code></td>
<td style="text-align:center">否</td>
</tr>
</tbody>
</table>
<h3>Drawer Events</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调函数参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">change</td>
<td style="text-align:left">value发生变化时触发</td>
<td style="text-align:left">变化后的值</td>
</tr>
<tr>
<td style="text-align:left">click-change</td>
<td style="text-align:left">点击导致value发生变化时触发</td>
<td style="text-align:left">变化后的值</td>
</tr>
<tr>
<td style="text-align:left">drag-change</td>
<td style="text-align:left">鼠标拖拽松开后导致值发生变化时发</td>
<td style="text-align:left">变化后的值</td>
</tr>
</tbody>
</table>

